	module.exports = {"data":{"post":{"html":"<p><a href=\"http://orangemedia.de/\">Orangemedia</a>, our partner firm in Hamburg, recently moved to the Stresemannstrasse. Not only do they have no parking spots, but it is near one of the most famous radar cameras in the city. Of course on my first visit to the new office I got nabbed on the way home, doing 37 km/h in the 30 km/zone. So I get to contribute &euro;15 to the Stresemannstrasse traffic reduction project, aka Freie und Hansestadt Hamburg Bu&szlig;geldstelle.</p>\n<p>I'm sure most Americans would find it funny to get a speeding ticket for 23 mph.</p>\n<p><b>Update</b> Let me add some background. The Stresemannstrasse is notorious as a narrow high traffic street in a residential area. Unfortunately it's the most direct route from downtown to the A7. In the '90s there were at least two kids killed by speeding trucks running red lights. After that it was made one lane with 30 km/h, but after von Beust took office it was restored to two lanes against the wishes of the residents. </p>","fields":{"title":"23 mph","date":"17 May 2006","url":"https://papascott.de/2006-05-17-23-mph/"}}},"pathContext":{"slug":"/2006-05-17-23-mph/","prev":{"fields":{"title":"World Cup","date":"16 May 2006","slug":"/2006-05-16-world-cup/"}},"next":{"fields":{"title":"Doc Test","date":"17 May 2006","slug":"/2006-05-17-doc-test/"}}}}

